import './AdminClienteDetail.css'

import { observer } from 'mobx-react-lite'
import React, { useState } from 'react'
import { MapContainer, Polyline, TileLayer } from 'react-leaflet'
import { RouteComponentProps } from 'react-router'

import { LinkButton, Page } from '../../components'
import { RUTAS, TIPO_ITINERARIO_STR } from '../../constants'
import { fechahora_fmt, minutos_to_str } from '../../helpers'
import {DetailViewProps, itinerario_marker, itinerario_popup} from '../../helpers-ts'
import { useStores } from '../../models/root-store/root-store-context'
import { HojaRutaApi, ItinerarioApi } from '../../services/api'

const numeral = require('numeral')
const moment = require('moment-timezone')

const defaultValues = {
  id: 0,
  fecha: '',
  transportista: {
    usuario: {
      first_name: '',
      last_name: '',
      nro_documento: '',
    },
  },
  itinerarios: [
    {
      fecha_hora_calculada_desde: '',
      orden: 0,
      tipo: 0,
      usuario: '',
      fabricante: '',
      direccion: '',
      ubicacion: '',
      distancia: 0,
      tiempo_en_transito: 0,
      tiempo_parada_calculado: 0,
      pedidos: [
        {
          referencia: '',
          id: 0,
        },
      ],
    },
  ],
  finalizado: false,
  tipo_vehiculo: {
    nombre: '',
  },
  inicio: {
    direccion: '',
    fecha_hora_calculada_desde: '',
  },
  fin: {
    direccion: '',
    fecha_hora_calculada_desde: '',
  },
  cantidad_retiros: 0,
  cantidad_entregas: 0,
  cantidad_paradas: 0,
  minutos_trabajo: 0,
  kilometros_recorrido: 0,
  tarifa_transportista: 0,
  vehiculo: {
    marca: '',
    modelo: '',
    nro_chapa: '',
  },
}

export const AdminHojaRutaDetail: React.FC<DetailViewProps> = observer(({ history, match }) => {
  const rootStore = useStores()
  const { adminStore } = rootStore
  const [HojaRuta, setHojaRuta] = useState<HojaRutaApi>()
  const [Ready, setReady] = useState(false)
  const [Center, setCenter] = useState({ lng: -57.63591, lat: -25.30066 })
  const [Polylines, setPolyline] = useState([[-57.63591, -25.30066]])
  const [IdHojaRuta, setIdHojaRuta] = useState(0)
  const idHojaRuta = match.params.id

  const setup = () => {
    setIdHojaRuta(parseInt(idHojaRuta))
    obtenerHojaRuta(parseInt(idHojaRuta))
    setReady(true)
  }

  React.useEffect(() => {
    setup()
  }, [idHojaRuta])

  const obtenerHojaRuta = (idHojaRuta: number) => {
    rootStore.environment.api.adminGetHojaRuta(idHojaRuta).then((result) => {
      if (result.kind === 'ok') {
        const hoja_ruta = result.hoja_ruta as HojaRutaApi
        let promLat = 0
        let promLng = 0
        // @ts-ignore
        const trazado_lineas = []
        // @ts-ignore
        setHojaRuta(hoja_ruta)
        // @ts-ignore
        hoja_ruta.itinerarios.forEach((itinerario) => {
          if (itinerario.ubicacion === '') {
            return
          }
          let ubicacion = itinerario.ubicacion.split(',')
          let lng = parseFloat(ubicacion[1])
          let lat = parseFloat(ubicacion[0])
          trazado_lineas.push([lat, lng])
          promLat += lat
          promLng += lng
        })
        setCenter({
          lng: promLng / hoja_ruta.itinerarios.length,
          lat: promLat / hoja_ruta.itinerarios.length,
        })
        // @ts-ignore
        setPolyline(trazado_lineas)
      }
    })
  }

  const detalleItinerario = (itinerario: any) => {
    adminStore.setItinerario(itinerario)
    history.push('/admin/hoja_ruta/itinerario/' + IdHojaRuta)
  }

  return (
    <Page login_required admin solo_admin>
      {HojaRuta && (
        <div className="page-wrapper" style={{ display: 'flex', height: '100vh', overflowY: 'scroll' }}>
          <div className="container-fluid background-white">
            <div className="row page-titles">
              <div className="col-md-5 col-12 align-self-center">
                <h3 className="text-themecolor">Hoja de ruta {HojaRuta.id}</h3>
              </div>
              <div className="col-md-7 col-12 align-self-center d-none d-md-block">
                <ol className="breadcrumb mb-0 p-0 bg-transparent float-right">
                  <li className="breadcrumb-item">
                    <a onClick={() => history.push('/admin/')}>Inicio</a>
                  </li>
                  <li className="breadcrumb-item">
                    <a href="/admin/hoja_ruta">Hojas de ruta</a>
                  </li>
                  <li className="breadcrumb-item active">{HojaRuta.id}</li>
                </ol>
              </div>
            </div>
            <div className="card">
              <div className="card-body">
                <div className={'row mb-4'}>
                  <div className={'col-4'}>
                    <strong>ID: </strong>
                    {HojaRuta.id}
                  </div>
                  <div className={'col-4'}>
                    <strong>Fecha: </strong>
                    {moment(HojaRuta.fecha).format('DD/MM/YYYY HH:mm')}
                  </div>
                  <div className={'col-4'}>
                    <strong>Finalizado: </strong>
                    {HojaRuta.finalizado ? 'Si' : 'No'}
                  </div>
                </div>
                <div className={'row mb-4'}>
                  <div className={'col-4'}>
                    <strong>Tipo de vehículo: </strong>
                    {HojaRuta.tipo_vehiculo?.nombre}
                  </div>
                  <div className={'col-4'}>
                    <strong>Driver: </strong>
                    {HojaRuta.transportista ? HojaRuta.transportista.usuario.first_name : 'NO ASIGNADO'}{' '}
                    {HojaRuta.transportista && HojaRuta.transportista.usuario.last_name}{' '}
                    {HojaRuta.transportista && HojaRuta.transportista.usuario.nro_documento}
                  </div>
                  <div className={'col-4'}>
                    <strong>Vehículo: </strong>
                    {HojaRuta.vehiculo ? HojaRuta.vehiculo.marca : ' NO ASIGNADO'}{' '}
                    {HojaRuta.vehiculo && HojaRuta.vehiculo.modelo} {HojaRuta.vehiculo && HojaRuta.vehiculo.numero_chapa}
                  </div>
                </div>
                <div className={'row mb-4'}>
                  <div className={'col-4'}>
                    <strong>Desde: </strong>
                    {HojaRuta.inicio.direccion} -{' '}
                    {moment(HojaRuta.inicio.fecha_hora_calculada_desde).format('DD/MM/YYYY HH:mm')}
                  </div>
                  <div className={'col-4'}>
                    <strong>Hasta: </strong>
                    {HojaRuta.fin.direccion} -{' '}
                    {moment(HojaRuta.fin.fecha_hora_calculada_desde).format('DD/MM/YYYY HH:mm')}
                  </div>
                </div>
                <div className={'row mb-4'}>
                  <div className={'col-4'}>
                    <strong>Cantidad de retiros: </strong>
                    {HojaRuta.cantidad_retiros}
                  </div>
                  <div className={'col-4'}>
                    <strong>Cantidad de entregas: </strong>
                    {HojaRuta.cantidad_entregas}
                  </div>
                  <div className={'col-4'}>
                    <strong>Cantidad total de paradas: </strong>
                    {HojaRuta.cantidad_paradas}
                  </div>
                </div>
                <div className={'row mb-4'}>
                  <div className={'col-4'}>
                    <strong>Tiempo de trabajo: </strong>
                    {minutos_to_str(HojaRuta.minutos_trabajo, false)}
                  </div>
                  <div className={'col-4'}>
                    <strong>Kilómetros a recorrer: </strong>
                    {HojaRuta.kilometros_recorrido.toFixed(2)} km.
                  </div>
                  <div className={'col-4'}>
                    <strong>Monto a cobrar por el driver: </strong>Gs.{' '}
                    {numeral(HojaRuta.tarifa_transportista).format('0,0')}
                  </div>
                </div>
              </div>
            </div>
            <h4>Itinerario</h4>
            <div className="card">
              <div className="card-body m-0 p-0">
                <table className={'table'}>
                  <thead>
                    <tr>
                      <th>#</th>
                      <th>Fecha/Hora</th>
                      <th>Tipo</th>
                      <th>Cliente/Proveedor</th>
                      <th>Dirección</th>
                      <th>Distancia (km.)</th>
                      <th>Tiempo</th>
                      <th>Pedido</th>
                    </tr>
                  </thead>
                  <tbody>
                    {HojaRuta.itinerarios.map((itinerario) => {
                      let tipo_itinerario_entrega = itinerario.tipo === 10 ? 'table-light' : ''
                      return (
                        <tr className={tipo_itinerario_entrega}>
                          <td>
                            <a onClick={() => detalleItinerario(itinerario)}>{itinerario.orden}</a>
                          </td>
                          <td>{fechahora_fmt(itinerario.fecha_hora_calculada_desde)}</td>
                          <td>{TIPO_ITINERARIO_STR[itinerario.tipo]}</td>
                          {itinerario.usuario && <td>{itinerario.usuario}</td>}
                          {!itinerario.usuario && <td>{itinerario.fabricante}</td>}
                          <td>
                            <a href={'https://maps.google.com/?q=' + itinerario.ubicacion + ''} target={'_blank'}>
                              {itinerario.direccion}
                            </a>
                          </td>
                          <td>{itinerario.distancia}</td>
                          <td>
                            {minutos_to_str(itinerario.tiempo_en_transito)} (+{' '}
                            {minutos_to_str(itinerario.tiempo_parada_calculado)})
                          </td>
                          <td>
                            {itinerario.pedidos.map((pedido, idx) => {
                              return (
                                <span key={`pedido-${idx}`}>
                                  <LinkButton href={RUTAS.ADMIN_PEDIDO_DETAIL(pedido.id)}>
                                    #{pedido.referencia}
                                  </LinkButton>
                                </span>
                              )
                            })}
                          </td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </div>
            </div>
            <h4>Recorrido</h4>
            <div style={{ height: 300, width: '100%' }} className={'mb-4'}>
              {Ready && (
                <MapContainer center={Center} zoom={12} style={{ height: 300, width: '100%' }}>
                  <TileLayer
                    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                    url="https://{s}.tile.osm.org/{z}/{x}/{y}.png"
                  />
                  <Polyline
                    // @ts-ignore
                    positions={Polylines}
                  />
                  {HojaRuta.itinerarios.map((itinerario) =>
                    itinerario_marker(itinerario as ItinerarioApi, itinerario_popup(itinerario as ItinerarioApi)),
                  )}
                </MapContainer>
              )}
            </div>
          </div>
        </div>
      )}
    </Page>
  )
})

export default AdminHojaRutaDetail
